.container {
  display: flex;
  width: 100%;
  height: 100vh;
}

.left {
  display: flex;
  flex: 1;
  padding: 70px;
}

.right {
  position: relative;
  display: flex;
  flex: 1;
  padding: 70px;
  background: url("../../assets/images/background/druida2.jpeg");
  background-size: cover;
  background-position: center;
}

.infoContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.title {
  text-align: right;
  color: #bc0e4d;
  font-size: 36px;
  font-family: "Laisha", sans-serif;
}

.description {
  width: 80%;
  font-size: 20px;
  line-height: 24px;
  font-family: "Laisha", sans-serif;
  text-align: end;
  color: #fff;
}

.backgroundTitle {
  z-index: 1;
  text-align: left;
  color: #fff;
  font-size: 50px;
  font-family: "Laisha", sans-serif;
  align-self: flex-end;
}

.overlayer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 600px;
  background: linear-gradient(to top, #151515, transparent);
}

@media screen and (max-width: 960px) {
  .container {
    flex-direction: column-reverse;
  }

  .left,
  .right {
    padding: 10px 30px;
  }

  .overlayer {
    bottom: -1px;
  }

  .infoContainer {
    align-items: flex-start;
  }

  .title {
    text-align: left;
  }

  .description {
    width: 100%;
    text-align: left;
  }

  .backgroundTitle {
    font-size: 40px;
  }
}
